const pagination = reactive({
  page: 1,
  limit: 10,
  total: 0,
});

const approvalForm = reactive({
  ghi_chu: '',
});

// Computed
const selectedTimesheetEmployee = computed(() => {
  if (!selectedTimesheet.value) return '';
  return getEmployeeName(selectedTimesheet.value.nhan_vien_id);
